import React, { FC, useState, useMemo } from "react";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts";

import { ItemScale } from "./DbScale";

type scaleProps = {
  data: ItemScale[];
};

function to_mmdd(x: Date) {
  return [x.getMonth() + 1, x.getDate()].join("/ ");
}

export const ChartMiBody: FC<scaleProps> = ({ data }) => {
  const x_key = "date";
  const y1_key = "weight_kg";
  const y2_key = "muscle";
  // Y2
  const [y2Key, setY2Key] = useState(y2_key);
  //  (x,y1)
  const key_candidates = useMemo(() => {
    if (data.length == 0) {
      return [];
    } else {
      return Object.keys(data[0]).filter((k) => k != x_key && k != y1_key);
    }
  }, [data]);

  // X ticks
  const ticks = useMemo(() => {
    const start_month = 2;
    const end_month = 7;
    const ticks = [];
    for (let m = start_month; m <= end_month; ++m) {
      ticks.push(new Date(`2022-${m}-01`).getTime());
    }
    return ticks;
  }, []);

  return (
    <div
      style={{
        fontSize: "1rem",
      }}
    >
      <ResponsiveContainer width="95%" height={300}>
        <LineChart data={data}>
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis
            allowDataOverflow={true}
            dataKey="date"
            type="number"
            domain={[ticks[0], ticks[-1]]}
            angle={-90}
            ticks={ticks}
            interval={0}
            textAnchor="end"
            tickFormatter={(date) => to_mmdd(new Date(date))}
            height={40}
          />
          <YAxis
            yAxisId="y1"
            tickCount={11}
            interval="preserveStart"
            domain={[
              (min: number) => Math.floor(min * 10 - 1) / 10,
              (max: number) => Math.ceil(max * 10 + 1) / 10,
            ]}
          />
          <YAxis
            yAxisId="y2"
            tickCount={11}
            interval="preserveStart"
            domain={[
              (min: number) => Math.floor(min * 10 - 1) / 10,
              (max: number) => Math.ceil(max * 10 + 1) / 10,
            ]}
            orientation="right"
          />
          <Tooltip
            label={"date"}
            labelFormatter={(date) => new Date(date).toLocaleDateString()}
            labelStyle={{
              color: "black",
            }}
          />
          <Legend
            verticalAlign="top"
            height={36}
            onClick={(e) => setY2Key(e.dataKey)}
          />
          <Line
            yAxisId="y1"
            type="monotone"
            dataKey={y1_key}
            dot={{ r: 1.5 }}
            stroke="#8884d8"
            key={y1_key}
            isAnimationActive={false}
          />
          {key_candidates.map((data_key) => (
            <Line
              yAxisId="y2"
              type="monotone"
              dataKey={data_key}
              dot={{ r: 1.5 }}
              stroke="#82ca9d"
              key={data_key}
              hide={y2Key != data_key}
              animationDuration={500}
            />
          ))}
        </LineChart>
      </ResponsiveContainer>
    </div>
  );
};
