import React, { useState, useEffect } from "react";
import { miScaleDB, ItemScale } from "./DbScale";
import { ChartMiBody } from "./Charts";
import SyncIcon from "@mui/icons-material/Sync";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import SyncDisabledIcon from "@mui/icons-material/SyncDisabled";
import Zoom from "@mui/material/Zoom";
import "./PageHealth.css";

export function PageHealth() {
  const [bodyTbl, setBodyTbl] = useState<ItemScale[]>([]);
  const [fetchStatus, setFetchStatus] = useState<number>(0);

  useEffect(() => {
    (async () => setBodyTbl(await miScaleDB.items.toArray()))();
  }, [fetchStatus]);

  useEffect(() => {
    (async () => {
      setFetchStatus(await sync_database());
    })();
  }, []);

  return (
    <div className="body">
      <div className="syncIcon">
        {
          <div>
            <Zoom in={fetchStatus == 0}>
              <SyncIcon color="action" />
            </Zoom>
            <Zoom in={fetchStatus == 1}>
              <CheckCircleOutlineIcon color="success" />
            </Zoom>
            <Zoom in={fetchStatus == 2}>
              <SyncDisabledIcon color="disabled" />
            </Zoom>
          </div>
        }
      </div>
      <ChartMiBody data={bodyTbl} />
    </div>
  );
}

async function sync_database() {
  let status = 0;
  try {
    const res = await fetch("http://192.168.10.103:7213/MiBodyScale");
    const data: Array<ItemScale> = await res.json();
    data.map((item) => {
      // convert date-string to unix-time
      item.date = new Date(item.date).getTime();
      miScaleDB.items.put(item);
    });
    status = 1;
  } catch (e) {
    console.log("error fetch from MiBodyScale");
    status = 2;
  }
  return status;
}
